.TimelineCard__container {
  position: relative;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  margin: 0 auto;
  opacity: 0;
  width: 90%; }
  @media (min-width: 1200px) {
    .TimelineCard__container {
      width: 50%; } }
  @media (min-width: 1200px) {
    .TimelineCard__container.js-expand-card {
      width: 100%; } }
  .TimelineCard__container.js-show-card {
    opacity: 1; }
    .TimelineCard__container.js-show-card .TimelineCard__joiner {
      opacity: 1; }

.TimelineCard__stack {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
  z-index: 100;
  margin-top: -12px; }
  @media (min-width: 1200px) {
    .TimelineCard__stack:hover {
      cursor: pointer; }
      .TimelineCard__stack:hover .TimelineCard__bottom-card {
        opacity: 1;
        margin-left: -50px; } }

@media (min-width: 1200px) {
