    -webkit-transform: scale(1.05);
            transform: scale(1.05); } }

@media (min-width: 1200px) {
  .TimelineCard__container:not(.js-expand-card) .TimelineCard__stack:hover .TimelineCard__bottom-card {
    -webkit-transform: rotate(2deg);
